import React, { useState, useEffect } from "react";

const Timer = ({ onTimeUp, currentQuestionIndex }) => {
    const [count, setCount] = useState(7); // Initialiser le compte  8
    const bpm = 120000 / 120; // La dure du Timer se cale en rythme sur le sample  120bpm

    useEffect(() => {
        const timer = setInterval(() => {
            if (count > 0) {
                setCount(count - 1); // Dcrmenter le compte
            } else {
                onTimeUp(); // Appeler la fonction lorsque le temps est coul
                setCount(7); // Rinitialiser le compte  8
            }
        }, bpm); // 1000ms = 1 seconde

        // Nettoyer l'intervalle lorsque le composant est dmont
        return () => clearInterval(timer);
    }, [count, onTimeUp]);

    useEffect(() => {
        // Rinitialiser le compte lorsque currentQuestionIndex change
        setCount(7);
    }, [currentQuestionIndex]);

    return <h1 className="big-time">{count}</h1>; // Afficher le compte
};

export default Timer;
