import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useLocation, useNavigate } from 'react-router-dom';
import AnimatedTextComponent from '../../../../AnimatedTextComponent';
import QuestSession from "../../../../QuestSession";

const OutroQuest1Carinatris = () => {
    const navigate                          = useNavigate();
    const location                          = useLocation();
    const userChoice                        = location.state.userChoice
    const { completeQuest, setQuestResult } = QuestSession();

    const textsSavior = [
        "",
        "Carinatris est sauve grce  toi",
        "Elle continuera de dvorer des mes  la pelle...",
        "Et probablement la tienne",
        "Mais grce  toi l'quilibre dominera l'ambiance universelle",
        "Peut-tre as-tu sauv de nombreuses mes ?",
    ]
    const textsMurderer = [
        "",
        "Carinatris n'est plus !",
        "De nombreuses mes ont t pargnes",
        "Mais pas la tienne",
        "Ni celles de nombreuses espces",
        "Car dsormais, le chaos rgne en matre."
    ]

    const texts = userChoice === 'savior' ? textsSavior : textsMurderer;
    
    // Set la dcision de l'utilisateur en session
    userChoice === 'savior' ? setQuestResult(0, 'Salvator') : setQuestResult(0, 'Assassinus');

    // Marque la qute comme termine
    completeQuest(0);

    const [currentTextIndex, setCurrentTextIndex] = useState(0);

    useEffect(() => {
        const nextText = () => {
            if (currentTextIndex < texts.length - 1) {
                setTimeout(() => {
                    setCurrentTextIndex(currentTextIndex + 1);
                }, 4500);
            } else {
                setTimeout(() => {
                    navigate('/absurdum');
                }, 4000);
            }
        };

        nextText();

        return () => clearTimeout();
    }, [currentTextIndex, texts.length]);

    return (
        <div className={`quest-container${location.state.userChoice === 'savior' ? '-savior' : '-murderer'}`}>
            <AnimatePresence>
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    transition={{ duration: 1 }}
                    className="quest-1-container"
                >
                    <div className={`animated-text-container`}
                        style={{ backgroundColor: 'transparent' }}>
                        <AnimatedTextComponent key={currentTextIndex} text={texts[currentTextIndex]} />
                        <div style={{
                            height: '222px',
                            display: 'flex',
                            justifyContent: 'space-around',
                            flexDirection: 'column',
                            alignItems: 'center',
                            width: '50%'
                        }}>
                        </div>
                    </div>
                </motion.div>
            </AnimatePresence>
        </div>
    );
}

export default OutroQuest1Carinatris;
