import React, { useEffect, useState } from 'react';
import AnimatedTextComponent from '../../AnimatedTextComponent';
import { Link } from 'react-router-dom';

const Outro = () => {
    const texts = [
        "Tu as dcrypt des secrets",
        "Affront tes peurs",
        "Dfi le temps",
        "Mais tu n'as toujours pas trouv le sens de ta qute",
        "C'est normal",
        "Tu as encore du chemin  parcourir",
        "Pour percer les secrets d'Absurdum..."
    ]

    const [showButton, setShowButton] = useState(false);

    // tat local pour suivre l'index du texte actuellement affich
    const [currentTextIndex, setCurrentTextIndex] = useState(0);

    useEffect(() => {
        // Fonction pour passer au texte suivant aprs la fin de l'animation
        const nextText = () => {
            if (currentTextIndex < texts.length - 1) {
                setTimeout(() => {
                    setCurrentTextIndex(currentTextIndex + 1);
                }, 4500);
            } else {
                setTimeout(() => {
                    setShowButton(true);
                }, 4000);
            }
        };

        // Appeler la fonction pour passer au texte suivant aprs la fin de l'animation
        nextText();

        return () => clearTimeout();
    }, [currentTextIndex, texts.length, showButton]);

    return (
        <>
            <div className="animated-text-container" style={{ backgroundColor: 'transparent', fontSize: '6em' }}>
                <AnimatedTextComponent key={currentTextIndex} text={texts[currentTextIndex]} />
                {showButton && (
                    <div style={{
                        height: '222px',
                        display: 'flex',
                        justifyContent: 'space-around',
                        flexDirection: 'column',
                        alignItems: 'center',
                        width: '50%'
                    }}>
                        <Link to="/" className='custom-link-red'>
                            Mauvais choix
                        </Link>
                        <Link to="/introgame" className='custom-link-blue'>
                            Bon choix
                        </Link>
                    </div>
                )}
            </div>
        </>
    );
};

export default Outro;
